var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"site-wrapper-reveal"},[_vm._ssrNode("<div class=\"church-about-area section-space--ptb_60\"><div class=\"container\"><div class=\"row align-items-center\"><div class=\"col-lg-8 ml-auto mr-auto\"><div class=\"about-tai-content\"><div class=\"section-title-wrap\"><h3 class=\"section-title--two left-style mb-30\">About Us</h3></div> <p>\n              A number of temples dedicated to ‘Vaishnavite’ deities were constructed during the\n              Hoysala time especially during the reign of Sri Vishnuvardhana. This king was\n              profoundly influenced by Sri Ramanujacharya who sought refugee in his kingdom after\n              fleeing from the Chola king who was keen on suppressing the followers of Vishnu (to\n              give prominence to Shiva). More importantly, it was here in Nuggehalli, along with\n              four other villages, the first of the settlement of the Srivaishnava Brahmins\n              migrating from the Chola ruled regions to Mysore (Karnataka) state occurred. Thus\n              the antiquity of the place was established. Sometime around this period the temples\n              including one dedicated to Lord Shiva came up at Nuggehalli. Over several years (to\n              be precise centuries), the Srivaishnavas migrated to various places &amp; settled mostly\n              in Karnataka. This led to a situation where taking care of the temple, performing\n              poojas &amp; conducting the annual car festival became a problem leading to skipping of\n              certain day to day rituals.\n            </p> <p>\n              Thus, we being the followers of Vishnu, our forefathers thought it to be fit to form\n              a samithi to take care of the temples at Nuggehalli. The Three temples Viz. Sri\n              Lakshmi Narashima, Srinivasa temples at Nuggehalli &amp; Keshava temple in Hebbalu were\n              brought into the preview of the samithi. The main object was to organise &amp; promote\n              proper celebrations of all the sevas at these temples &amp; to augment finance to\n              conduct the daily rituals apart from rathotsava &amp; to wholeheartedly support the\n              Dharmadarshi committee in this regard.\n            </p></div></div></div></div></div> <div class=\"church-about-area section-space--ptb_60\"><div class=\"container\"><div class=\"row align-items-center\"><div class=\"col-lg-8 ml-auto mr-auto\"><div class=\"about-tai-content\"><div class=\"section-title-wrap\"><h3 class=\"section-title--two left-style mb-30\">Membership</h3></div> <p>\n              The membership fee is INR 500/- until further amendment. The tenure of the MC is for\n              3 years. The members in the managing committee has been decreased to 7 of which 2\n              are women.\n            </p></div></div></div></div></div> <div class=\"church-about-area section-space--ptb_60\"><div class=\"container\"><div class=\"row align-items-center\"><div class=\"col-lg-8 ml-auto mr-auto\"><div class=\"about-tai-content\"><div class=\"section-title-wrap\"><h3 class=\"section-title--two left-style mb-30\">Managing Committee</h3></div> <table><tbody><tr><td data-label>President - N Govindraj</td></tr> <tr><td data-label>Vice President - C B Shankar</td></tr> <tr><td data-label>Secretary - N R Gopal</td></tr> <tr><td data-label>Treasurer - Raadhika Prasanna</td></tr></tbody></table></div></div></div></div></div> <div class=\"church-about-area section-space--ptb_60\"><div class=\"container\"><div class=\"row align-items-center\"><div class=\"col-lg-8 ml-auto mr-auto\"><div class=\"about-tai-content\"><div class=\"section-title-wrap\"><h3 class=\"section-title--two left-style mb-30\">Special Invitees</h3></div> <table><tbody><tr><td data-label>C D Narasimhan</td></tr> <tr><td data-label>K N Rajagopal</td></tr></tbody></table></div></div></div></div></div> <div class=\"church-abou